import { formRules } from "../../../../shared/formRules";
import { InputLayout, FullSelectLayout } from "../../../shared/Layouts";
import { Space } from "antd";
import { CODE_RUBRIQUES } from "../../../../shared/rubrique";
import { EltRubriqueEntity } from "../../../../entities/eltRubrique.entity";
import { OrganismeEntity } from "../../../../entities/organisme.entity";
import { Rule } from "antd/es/form";

export const UserStatutForm = ({
  user,
  readOnly,
  size,
  nextTab,
}: {
  user?: any;
  readOnly?: boolean;
  size?: number;
  nextTab?: (val: any) => void;
}) => {
  const localOrganismes = localStorage.getItem("organismes");
  const organismes = localOrganismes
    ? (JSON.parse(localOrganismes) as OrganismeEntity[])
    : [];
  const formation_sanitaires = organismes.filter(
    (org) => org.type_organisme === CODE_RUBRIQUES.FORMATION_SANITAIRE
  );

  const reseau_trt = organismes.filter(
    (org) => org.type_organisme === CODE_RUBRIQUES.RESEAU_TRAUMATOLOGIE
  );

  const localEltRub = localStorage.getItem("eltRubriques");
  const eltRubriques = localEltRub
    ? (JSON.parse(localEltRub) as EltRubriqueEntity[])
    : [];

  return (
    <div className="row">
      <InputLayout
        label="N° ONMC"
        name="id"
        rules={[
          {
            max: 11,
            required: true,
            message: "Ce numéro n'est pas valide au Cameroun",
          } as Rule,
        ]}
        placeholder="numéro de l'Ordre National des Medecins"
        value={user?.id}
        disabled={readOnly}
      />
      <FullSelectLayout
        label="Qualification"
        name="qualification"
        placeholder="Préciser votre spécialité"
        key={"medecin-qualification"}
        items={eltRubriques.filter(
          (elt: EltRubriqueEntity) =>
            elt.rubrique === CODE_RUBRIQUES.QUALIFICATION
        )}
        rules={[formRules.ruleRequired]}
        value={user?.qualification}
        disabled={readOnly}
      />
      <InputLayout
        label="Années d'expériences"
        name="nb_experience"
        size={size}
        value={user?.nb_experience}
        disabled={readOnly}
        rules={[
          {
            required: false,
            type: "number",
          } as Rule,
        ]}
        type="number"
      />
      <FullSelectLayout
        label="Affiliations"
        placeholder="Membre d'un réseau de traumatologie"
        name="reseau_trt"
        multiple={true}
        key={"medecin-reseau"}
        items={reseau_trt}
        rules={[formRules.ruleNoRequired]}
        size={size}
        value={user?.organismes.filter((org: number | string) =>
          reseau_trt.find((elt) => elt.id === org)
        )}
        disabled={readOnly}
      />
      <FullSelectLayout
        label="Lieu d'exercice"
        name="formation_sanitaire"
        placeholder="Selectionner les formations sanitaires où vous exercez"
        multiple={true}
        key={"medecin-fs"}
        items={formation_sanitaires}
        rules={[formRules.ruleNoRequired]}
        size={size}
        value={user?.organismes.filter((org: number | string) =>
          formation_sanitaires.find((elt) => elt.id === org)
        )}
        disabled={readOnly}
      />
      <Space className="ms-auto col-md-6 mt-2">
        <button
          type="button"
          className="btn btn-outline-secondary rounded-pill ml-3 ms-auto"
          onClick={() => nextTab?.("1")}
        >
          Precedent
        </button>
        {!readOnly ? (
          <button type="submit" className="btn btn-primary rounded-pill">
            Enregistrer
          </button>
        ) : (
          <span></span>
        )}
      </Space>
    </div>
  );
};
