import { IMAGES } from "../../../shared/images";
import {
  FaOutdent,
  FaSearch,
  FaUserAlt,
  FaUserCog,
  FaHandsHelping,
  FaSignOutAlt,
} from "react-icons/fa";
import { Image } from "antd";
import { Link } from "react-router-dom";
import { SearchBar } from "../SearchBar";
import { useState } from "react";
import { BS_BLUE } from "../../../shared/colors";
import { APP_TITLE } from "../../../shared/constants";

export const Navbar = ({
  homeLink,
  forLogin,
  shrink,
  setShrink,
}: {
  homeLink: string;
  forLogin?: boolean;
  shrink?: boolean;
  setShrink?: (val: boolean) => void;
}) => {
  const [searchShrink, setSearchShrink] = useState(false);
  return (
    <header id="header" className="header fixed-top d-flex align-items-center">
      <div className="d-flex align-items-center justify-content-between">
        <Link to={homeLink} className="logo d-flex align-items-center">
          <Image src={IMAGES.logo} alt="logo" preview={false} />
          <span className="d-none d-lg-block">{APP_TITLE}</span>
        </Link>
        {forLogin ? (
          <p></p>
        ) : (
          <i>
            <FaOutdent
              size={34}
              className="toggle-sidebar-btn"
              onClick={() => setShrink?.(!shrink)}
            />
          </i>
        )}
      </div>

      {/* <SearchBar shrinked={searchShrink} /> */}

      <nav className="header-nav ms-auto">
        <ul className="d-flex align-items-center">
          <li className="nav-item d-block d-lg-none">
            <div className="nav-link nav-icon search-bar-toggle">
              <FaSearch
                className="search-bar-toggle"
                onClick={() => setSearchShrink(!searchShrink)}
              />
            </div>
          </li>
          {forLogin ? (
            <span></span>
          ) : (
            <div>
              <li className="nav-item dropdown pe-3">
                <Link
                  className="nav-link nav-profile d-flex align-items-center pe-0"
                  to="/next"
                  data-bs-toggle="dropdown"
                >
                  <img
                    src={IMAGES.defaultImage}
                    alt="Profile"
                    className="rounded-circle"
                  />
                  <span className="d-none d-md-block dropdown-toggle ps-2">
                    Admin
                  </span>
                </Link>

                <ul className="dropdown-menu dropdown-menu-end dropdown-menu-arrow profile ">
                  <li className="dropdown-header">
                    <h6>Admin</h6>
                    <span>Traumatologue</span>
                  </li>
                  <li>
                    <hr className="dropdown-divider" />
                  </li>

                  <li>
                    <Link
                      className="dropdown-item d-flex align-items-center"
                      to="#"
                    >
                      <i>
                        <FaUserAlt color={BS_BLUE} size={20} />
                      </i>
                      <span>My Profile</span>
                    </Link>
                  </li>
                  <li>
                    <hr className="dropdown-divider" />
                  </li>

                  <li>
                    <Link
                      className="dropdown-item d-flex align-items-center"
                      to="#"
                    >
                      <i>
                        <FaUserCog color={BS_BLUE} size={20} />
                      </i>
                      <span>Account Settings</span>
                    </Link>
                  </li>
                  <li>
                    <hr className="dropdown-divider" />
                  </li>

                  <li>
                    <Link
                      className="dropdown-item d-flex align-items-center"
                      to="#"
                    >
                      <i>
                        <FaHandsHelping color={BS_BLUE} size={20} />
                      </i>
                      <span>Need Help?</span>
                    </Link>
                  </li>
                  <li>
                    <hr className="dropdown-divider" />
                  </li>

                  <li>
                    <Link
                      className="dropdown-item d-flex align-items-center"
                      to="/"
                    >
                      <i>
                        <FaSignOutAlt color={BS_BLUE} size={20} />
                      </i>
                      <span>Sign Out</span>
                    </Link>
                  </li>
                </ul>
              </li>
            </div>
          )}
        </ul>
      </nav>
    </header>
  );
};
