import { EltRubriqueEntity } from "../entities/eltRubrique.entity";
import { OrganismeEntity } from "../entities/organisme.entity";

export const APP_TITLE = "RCT";

export const sexeItems = [
  { id: "M", code: "M", designation: "Homme", description: "" },
  { id: "F", code: "M", designation: "Femme", description: "" },
] as EltRubriqueEntity[];

export const openFormMode = {
  readOnly: 1,
  edit: 2,
};

// ---------------- LOCAL VALUES ---------------
const localOrganismes = localStorage.getItem("organismes");
const localEltRub = localStorage.getItem("eltRubriques");
const localPays = localStorage.getItem("pays");

export const PAYS = localPays ? JSON.parse(localPays) : [];

export const ELT_RUBRIQUES = localEltRub
  ? (JSON.parse(localEltRub) as EltRubriqueEntity[])
  : [];

export const ORGANISMES = localOrganismes
  ? (JSON.parse(localOrganismes) as OrganismeEntity[])
  : [];
