import { Form, Spin, Tabs, TabsProps, notification } from "antd";
import { useState } from "react";
import { createMedecin } from "../../network";
import { FaUserNurse } from "react-icons/fa";
import { BsPersonBoundingBox } from "react-icons/bs";
import { MedecinEntity } from "../../../../entities/medecin.entity";
import { UserPersonnelForm } from "./UserPersonnelForm";
import { UserStatutForm } from "./UserStatutForm";
import { useHistory } from "react-router-dom";
import { ROUTES } from "../../../../routes";

export const AddUserComponent = ({
  user,
  readOnly,
  setIsRegistered,
  registration,
  closeModal,
  size,
}: {
  user?: MedecinEntity;
  readOnly?: boolean;
  registration?: boolean;
  setIsRegistered?: (val: boolean) => void;
  closeModal?: () => void;
  size?: number;
}) => {
  const router = useHistory();
  const [form] = Form.useForm();
  const [activeTab, setActiveTab] = useState("1");
  const [isStatutOn, setIsStatutOn] = useState(readOnly ? true : false);
  const [isLoadingForm, setIsLoadingForm] = useState(false);

  const handleNextTab = () => {
    if (!readOnly) {
      // en mode écriture
      form
        .validateFields(["nom", "prenom", "sexe", "email", "telephone"], {
          validateOnly: false,
        })
        .then((value) => {
          setActiveTab("2");
          setIsStatutOn(true);
        })
        .catch((error) => {
          setIsStatutOn(false);
          validationErrorAlert();
        });
    } else {
      // affichage du formulaire en mode lecture
      setActiveTab("2");
    }
  };

  const validationErrorAlert = () =>
    notification.error({
      message: "Attention",
      description:
        "Veuillez remplir tous les champs obligatoires pour completer votre demande d'adhésion",
    });

  const items: TabsProps["items"] = [
    {
      key: "1",
      label: (
        <span>
          <BsPersonBoundingBox className="mx-3" size={28} />
          Données personnelles
        </span>
      ),
      children: (
        <UserPersonnelForm
          user={user}
          readOnly={readOnly}
          nextTab={handleNextTab}
        />
      ),
    },
    {
      key: "2",
      label: (
        <span>
          <FaUserNurse className="mx-3" size={28} />
          Statut médical
        </span>
      ),
      disabled: isStatutOn ? false : true,
      children: (
        <UserStatutForm
          user={user}
          readOnly={readOnly}
          nextTab={setActiveTab}
        />
      ),
    },
  ];

  return (
    <Spin spinning={isLoadingForm}>
      <div>
        <Form
          name="medecin-info-form"
          key="medecin-info-form"
          form={form}
          className="col-md-11"
          labelCol={{ span: 8 }}
          scrollToFirstError
          onFinish={async (data) => {
            setIsLoadingForm(true);
            const medecinToPost = {
              id: data.id,
              nom: data.nom,
              prenom: data.prenom,
              sexe: data.sexe,
              date_naissance: data.date_naissance
                ? new Date(data.date_naissance).toLocaleString()
                : null,
              // date_fonction: new Date(data.date_fonction).toLocaleString(),
              nb_experience: data.nb_experience,
              qualification: data.qualification,
              email: data.email,
              telephone: data.telephone,
              nationalite: data.nationalite,
            };
            const organismesToPost = data.reseau_trt?.concat(
              data.formation_sanitaire
            );
            const dataToPost = {
              medecin: medecinToPost,
              organismes: organismesToPost,
            };
            console.log("data to send : ", dataToPost);
            await createMedecin(dataToPost)
              .then(async (data) => {
                if (data.success) {
                  setIsRegistered?.(true);
                  notification.success({
                    message: "Succès",
                    description: "Medecin enregistré avec succès",
                  });
                  form.resetFields();
                  if (registration) closeModal?.();
                  else router.push(ROUTES.USER_PAGE.LIST_USER);
                } else {
                  notification.error({
                    message: "Erreur",
                    description: data.message,
                  });
                }
              })
              .catch((error) => {
                notification.error({
                  message: "Erreur",
                  description: error.message,
                });
              });
            setIsLoadingForm(false);
          }}
          onFinishFailed={() => validationErrorAlert()}
        >
          <Tabs
            defaultActiveKey="1"
            items={items}
            activeKey={activeTab}
            onTabClick={(key, e) => {
              if (activeTab === "1") handleNextTab();
              else setActiveTab(key);
            }}
          />
        </Form>
      </div>
    </Spin>
  );
};
