import { Rule } from "antd/es/form";

const ruleNoRequired = {
  required: false,
  message: "Veuillez completer ce champ",
};

const ruleRequired = {
  required: true,
  message: "Veuillez completer ce champ",
};

const validCmrPhoneNumber = new RegExp("^(6|2)(2|3|[5-9])[0-9]{7}$");
const validPassword = new RegExp("^(?=.*?[A-Za-z])(?=.*?[0-9]).{6,}$");
const validEmail = new RegExp("^[a-zA-Z0-9._:$!%-]+@[a-zA-Z0-9.-]+.[a-zA-Z]$");

const cmrPhoneRule = (required?: boolean) => {
  return {
    len: 9,
    type: "regexp",
    pattern: validCmrPhoneNumber,
    required: required,
    message: "Ce numéro n'est pas valide au Cameroun",
  } as Rule;
};

const passwordRule = (required?: boolean) => {
  return {
    type: "regexp",
    pattern: validPassword,
    required: required,
    message: "Ce mot de passe n'est pas valide",
  } as Rule;
};

const emailRule = (required?: boolean) => {
  return {
    type: "regexp",
    pattern: validEmail,
    required: required,
    message: "Cette adresse mail n'est pas valide",
  } as Rule;
};

export const formRules = {
  ruleNoRequired,
  ruleRequired,
  passwordRule,
  emailRule,
  cmrPhoneRule,
};
