import { MedecinEntity } from "../../../../entities/medecin.entity";
import { sexeItems } from "../../../../shared/constants";
import { formRules } from "../../../../shared/formRules";
import {
  InputLayout,
  RadioLayout,
  FullSelectLayout,
  DateLayout,
} from "../../../shared/Layouts";

export const UserPersonnelForm = ({
  user,
  readOnly,
  size,
  nextTab,
}: {
  user?: MedecinEntity;
  readOnly?: boolean;
  size?: number;
  nextTab?: () => void;
}) => {
  const localPays = localStorage.getItem("pays");
  const pays = localPays ? JSON.parse(localPays) : [];

  return (
    <div className="row">
      <InputLayout
        label="Nom"
        name="nom"
        rules={[formRules.ruleRequired]}
        size={size}
        value={user?.nom}
        disabled={readOnly}
      />
      <InputLayout
        label="Prenom"
        name="prenom"
        rules={[formRules.ruleRequired]}
        size={size}
        value={user?.prenom}
        disabled={readOnly}
      />
      <RadioLayout
        label="Sexe"
        name="sexe"
        items={sexeItems}
        rules={[formRules.ruleRequired]}
        defaultValue={user?.sexe}
        disabled={readOnly}
      />
      <FullSelectLayout
        label="Nationalité"
        name="nationalite"
        key={"medecin-nationalite"}
        items={pays}
        rules={[formRules.ruleNoRequired]}
        className="d-none"
        size={size}
        value={
          pays.find((nat: any) => nat.id === user?.nationalite)?.designation
        }
        disabled={readOnly}
      />
      <DateLayout
        label="Date de naissance"
        name="date_naissance"
        className="d-none"
        size={size}
        value={user?.date_naissance}
        disabled={readOnly}
      />
      <InputLayout
        label="Adresse electronique"
        name="email"
        rules={[
          {
            type: "regexp",
            pattern: new RegExp(
              "^[a-zA-Z0-9._:$!%-]+@[a-zA-Z0-9.-]+.[a-zA-Z]$"
            ),
            required: true,
            message: "Cette adresse mail n'est pas valide",
          },
        ]}
        size={size}
        value={user?.adresse_mail}
        disabled={readOnly}
      />
      <InputLayout
        label="Numéro de télephone"
        name="telephone"
        rules={[formRules.cmrPhoneRule(true)]}
        size={size}
        value={user?.telephone}
        disabled={readOnly}
      />
      <button
        type="button"
        className="btn btn-primary rounded-pill mt-3 col-md-3 ms-auto"
        onClick={() => nextTab?.()}
      >
        Suivant
      </button>
    </div>
  );
};
