//========================= RUBRIQUE TAGS & BUTTON NAVIGATION ==============
export const RubriqueTag = ({
  name,
  label,
  bscolor,
  activeKey,
  setActiveKey,
  statut,
}: {
  name: number;
  label: string;
  bscolor: BS_COLOR;
  activeKey: number;
  setActiveKey: (name: number) => void;
  statut: number; // la dernière étape débloqué, spécifie à quel niveau le patient se trouve dans le processus
}) => {
  return (
    <button
      className={
        activeKey === name
          ? "btn rounded-pill btn-" + bscolor
          : "btn rounded-pill btn-outline-" + bscolor
      }
      key={"rbtag-" + name}
      data-bs-toggle="collapse"
      data-bs-target={"#" + name}
      aria-expanded="false"
      aria-controls={"rub-ctrl-" + name}
      onClick={() => {
        console.log(name, "  ", statut);
        if (name <= statut) setActiveKey(name);
        else {
          notification.warning({
            message: "Attention",
            description: "Veuillez d'abord completer la section précedente",
            placement: "top",
          });
        }
      }}
    >
      {label}
    </button>
  );
};

export const RubriqueTitle = ({ title }: { title: string }) => (
  <strong className="text-uppercase text-center mt-1 mb-2">
    <h4 className="">{title}</h4>
  </strong>
);

export const RubriqueNavigate = () => {
  const router = useHistory();
  return (
    <Space className="ms-auto">
      <button
        type="button"
        className="btn btn-outline-secondary ml-3 rounded-pill"
        onClick={() => router.push(ROUTES.REGISTRE_PAGE.PRINCIPAL)}
      >
        Annuler
      </button>
      <button type="submit" className="btn btn-primary rounded-pill">
        Enregistrer
      </button>
    </Space>
  );
};

// le statut du patient : correspond au statut de son dernier dossier s'il existe
export const dossierStatutTag = (val: any) => {
  var tag = (
    <Tag bordered={false} color={BS_BLUE} className="rounded-pill">
      inconnu
    </Tag>
  );
  val === DOSSIER_STATUT.nouveau
    ? (tag = (
        <Tag bordered={false} color="processing" className="rounded-pill">
          nouveau
        </Tag>
      ))
    : val === DOSSIER_STATUT.decede
    ? (tag = (
        <Tag bordered={false} color="error" className="rounded-pill">
          decedé
        </Tag>
      ))
    : val === DOSSIER_STATUT.clos
    ? (tag = (
        <Tag bordered={false} color="error" className="rounded-pill">
          clos
        </Tag>
      ))
    : val < DOSSIER_STATUT.anamnese
    ? (tag = (
        <Tag bordered={false} color="gold" className="rounded-pill">
          anamnèse
        </Tag>
      ))
    : val === DOSSIER_STATUT.anamnese // en instance de traitement
    ? (tag = (
        <Tag bordered={false} color="cyan" className="rounded-pill">
          en attente
        </Tag>
      ))
    : val < DOSSIER_STATUT.traitement
    ? (tag = (
        <Tag bordered={false} color="green-inverse" className="rounded-pill">
          traitement
        </Tag>
      ))
    : val < DOSSIER_STATUT.suivi
    ? (tag = (
        <Tag bordered={false} color="cyan" className="rounded-pill">
          suivi
        </Tag>
      ))
    : val < DOSSIER_STATUT.rehabilitation
    ? (tag = (
        <Tag bordered={false} color="magenta" className="rounded-pill">
          rehabilitation
        </Tag>
      ))
    : (tag = (
        <Tag bordered={false} color="default" className="rounded-pill">
          inconnu
        </Tag>
      ));

  return tag;
};

// mode de prise en charge du patient
export const modePCTag = (val: any, legend?: boolean) => {
  var tag = (
    <Tag bordered={false} color="default" className="rounded-pill">
      inconnu
    </Tag>
  );
  switch (val) {
    case DOSSIER_PC_MODE.immediate: // prise en charge immediate
      tag = (
        <Space>
          <Tag
            bordered={false}
            color="pink"
            className="rounded-pill"
            style={{ cursor: "help" }}
          >
            <Tooltip title="Prise en charge immediate">
              {" "}
              <b> PCI </b>{" "}
            </Tooltip>
          </Tag>
          {legend ? (
            <span className="text-muted"> Prise en charge immediate </span>
          ) : (
            ""
          )}
        </Space>
      );
      break;
    case DOSSIER_PC_MODE.differe: // prise en charge differée
      tag = (
        <Space>
          <Tag
            bordered={false}
            color="error"
            className="rounded-pill"
            style={{ cursor: "help" }}
          >
            <Tooltip title="Prise en charge differée">
              {" "}
              <b> PCD </b>{" "}
            </Tooltip>
          </Tag>
          {legend ? (
            <span className="text-muted"> Prise en charge differée </span>
          ) : (
            ""
          )}
        </Space>
      );
      break;
    case DOSSIER_PC_MODE.retardee: // prise en charge retardée
      tag = (
        <Space>
          <Tag
            bordered={false}
            color="succes"
            className="rounded-pill"
            style={{ cursor: "help" }}
          >
            <Tooltip title="Prise en charge retardée">
              {" "}
              <b> PCR </b>{" "}
            </Tooltip>
          </Tag>
          {legend ? (
            <span className="text-muted"> Prise en charge retardée </span>
          ) : (
            ""
          )}
        </Space>
      );
      break;
    case DOSSIER_PC_MODE.sequelles: // prise en charge des sequelles
      tag = (
        <Space>
          <Tag
            bordered={false}
            color="error"
            className="rounded-pill"
            style={{ cursor: "help" }}
          >
            <Tooltip title="Prise en charge differée">
              {" "}
              <b> PCQ </b>{" "}
            </Tooltip>
          </Tag>
          {legend ? (
            <span className="text-muted"> Prise en charge des sequelles </span>
          ) : (
            ""
          )}
        </Space>
      );
      break;
    case DOSSIER_PC_MODE.complications: // prise en charge des complications
      tag = (
        <Space>
          <Tag
            bordered={false}
            color="processing"
            className="rounded-pill"
            style={{ cursor: "help" }}
          >
            <Tooltip title="Prise en charge des complications">
              {" "}
              <b> PCD </b>{" "}
            </Tooltip>
          </Tag>
          {legend ? (
            <span className="text-muted">
              {" "}
              Prise en charge des complications{" "}
            </span>
          ) : (
            ""
          )}
        </Space>
      );
      break;
    default: // case 0 and others
      break;
  }
  return tag;
};

