// for input layout
export const InputLayout = ({
  label,
  multiple,
  size,
  name,
  rules,
  className,
  disabled,
  value,
  style,
  bordered,
  placeholder,
  type,
}: {
  label: string;
  multiple?: boolean;
  size?: number;
  name: string;
  rules?: any;
  className?: string;
  disabled?: boolean;
  value?: string;
  style?: React.CSSProperties;
  bordered?: boolean;
  placeholder?: string;
  type?: "number" | "password";
}) => {
  return type === "number" ? (
    <Form.Item
      label={label}
      name={name}
      rules={rules}
      className={className}
      wrapperCol={{ span: size }}
      style={style}
      initialValue={value}
    >
      <InputNumber
        disabled={disabled}
        bordered={bordered}
        placeholder={placeholder}
      />
    </Form.Item>
  ) : multiple ? (
    <Form.Item
      label={label}
      name={name}
      rules={rules}
      className={className}
      wrapperCol={{ span: size }}
      style={style}
      initialValue={value}
    >
      <TextArea rows={3} disabled={disabled} placeholder={placeholder} />
    </Form.Item>
  ) : (
    <Form.Item
      label={label}
      name={name}
      rules={rules}
      className={className}
      wrapperCol={{ span: size }}
      style={style}
      initialValue={value}
    >
      <Input
        // readOnly={disabled}
        disabled={disabled}
        bordered={bordered}
        // defaultValue={value}
        placeholder={placeholder}
      />
    </Form.Item>
  );
};

export const DateLayout = ({
  className,
  label,
  name,
  rules,
  value,
  size,
  disabled,
  formRef,
}: {
  className?: string;
  label: string;
  name: string;
  rules?: any;
  value?: any;
  size?: number;
  disabled?: boolean;
  formRef?: FormInstance<any>;
}) => {
  return disabled ? (
    <Form.Item
      label={label}
      name={name}
      rules={rules}
      className={className}
      wrapperCol={{ span: size }}
    >
      <Input readOnly={disabled} defaultValue={value} />
    </Form.Item>
  ) : (
    <Form.Item
      label={label}
      name={name}
      rules={rules}
      className={className}
      wrapperCol={{ span: size }}
    >
      <DatePicker showTime />
    </Form.Item>
  );
};

