// for select one
export const SimpleSelectLayout = ({
  tags,
  className,
  label,
  name,
  items,
  rules,
  handleSelect,
}: {
  tags?: boolean;
  className: string;
  label: string;
  name: string;
  items: any[];
  rules: any;
  handleSelect?: (val: any) => void;
}) => {
  return (
    <Form.Item
      key={"formitem-" + label}
      label={label}
      name={name}
      rules={rules}
      className={className}
    >
      <Select placeholder="" onChange={handleSelect}>
        {items.map((item: string) => (
          <Select.Option value={item} key={"selectopt-" + item}>
            {item.charAt(0).toUpperCase() + item.slice(1)}
          </Select.Option>
        ))}
      </Select>
    </Form.Item>
  );
};

// for select one
export const SelectLayout = ({
  size, // number of colon for the input Form.Item
  multiple,
  className,
  defaultValue,
  label,
  name,
  items,
  rules,
  handleSelect,
}: {
  size?: number;
  multiple?: boolean;
  className?: string;
  defaultValue?: any;
  label: string;
  name: string;
  items: any[];
  rules?: any;
  handleSelect?: (val: any) => void;
}) => {
  const options: SelectProps["options"] = [];

  // TO DO : default value for Select using SelectProps property

  // eslint-disable-next-line array-callback-return
  items.map((i) => {
    options.push({
      value: i.id,
      label: i.designation,
    });
  });

  const handleSelectChange = (value: any) => {
    const optSelected = options.find((opt) => opt.value === value);
    handleSelect?.(optSelected?.label);
  };

  return (
    <Form.Item
      label={label}
      name={name}
      rules={rules}
      className={className}
      wrapperCol={{ span: size }}
    >
      {multiple ? (
        <Select
          mode="multiple"
          defaultValue={defaultValue}
          showSearch
          placeholder=""
          optionFilterProp="children"
          filterOption={(input: any, option: any) =>
            (option?.label ?? "").toLowerCase().includes(input)
          }
          filterSort={(optionA: any, optionB: any) =>
            (optionA?.label ?? "")
              .toLowerCase()
              .localeCompare((optionB?.label ?? "").toLowerCase())
          }
          options={options}
          onChange={handleSelectChange}
        />
      ) : (
        <Select
          showSearch
          placeholder=""
          optionFilterProp="children"
          filterOption={(input: any, option: any) =>
            (option?.label ?? "").toLowerCase().includes(input)
          }
          filterSort={(optionA: any, optionB: any) =>
            (optionA?.label ?? "")
              .toLowerCase()
              .localeCompare((optionB?.label ?? "").toLowerCase())
          }
          options={options}
          onChange={handleSelectChange}
        />
      )}
    </Form.Item>
  );
};

// for select one
export const FullSelectLayout = ({
  size, // number of colon for the input Form.Item
  multiple,
  className,
  defaultValue,
  label,
  name,
  items,
  rules,
  handleSelect,
  placeholder,
  value,
  disabled,
}: {
  size?: number;
  multiple?: boolean;
  className?: string;
  defaultValue?: any;
  label: string;
  name: string;
  items: any;
  rules?: any;
  handleSelect?: (val: any) => void;
  placeholder?: string;
  value?: any;
  disabled?: boolean;
}) => {
  const options: SelectProps["options"] = [];

  // eslint-disable-next-line array-callback-return
  items.map((i: any) => {
    i.designation
      ? options.push({
          value: i.id,
          label: i.designation,
        })
      : options.push({
          value: i.id,
          label: i.nom + " " + i.prenom,
        });
  });

  const handleSelectChange = (value: any) => {
    handleSelect?.(value);
  };

  return (
    <Form.Item
      label={label}
      name={name}
      rules={rules}
      className={className}
      wrapperCol={{ span: size }}
      initialValue={value}
      style={{ color: "#181818" }}
    >
      {multiple ? (
        <Select
          mode="multiple"
          defaultValue={defaultValue}
          showSearch
          placeholder={placeholder}
          optionFilterProp="children"
          defaultActiveFirstOption
          filterOption={(input: any, option: any) =>
            (option?.label ?? "").toLowerCase().includes(input)
          }
          filterSort={(optionA: any, optionB: any) =>
            (optionA?.label ?? "")
              .toLowerCase()
              .localeCompare((optionB?.label ?? "").toLowerCase())
          }
          options={options}
          onChange={handleSelectChange}
          disabled={disabled}
        />
      ) : (
        <Select
          showSearch
          placeholder={placeholder}
          optionFilterProp="children"
          defaultActiveFirstOption
          filterOption={(input: any, option: any) =>
            (option?.label ?? "").toLowerCase().includes(input)
          }
          filterSort={(optionA: any, optionB: any) =>
            (optionA?.label ?? "")
              .toLowerCase()
              .localeCompare((optionB?.label ?? "").toLowerCase())
          }
          options={options}
          onChange={handleSelectChange}
          disabled={disabled}
        />
      )}
    </Form.Item>
  );
};

// for checkbox (select many)
export const CheckboxLayout = ({
  label,
  items,
}: {
  label: string;
  items: any[];
}) => {
  return (
    <div className="row">
      <label htmlFor={label} className="">
        {label}
      </label>
      <div className="">
        <Row gutter={[1, 1]} className="mt-2 border">
          {items.map((item) => (
            <Col span={6} className="px-2">
              <Form.Item
                key={"selectopt-" + item}
                name={item}
                valuePropName="checked"
              >
                <Checkbox>{item}</Checkbox>
              </Form.Item>
            </Col>
          ))}
        </Row>
      </div>
    </div>
  );
};

//========================== SCORES LAYOUTS ===============================
export const ScoreFormValue = ({
  scores,
  selectedScore,
  setSelectedScore,
}: {
  scores: any;
  selectedScore: string;
  setSelectedScore: (val: string) => void;
}) => {
  return (
    <Form layout="inline" labelCol={{ span: 9 }}>
      <Row gutter={[70, 16]} className="px-3">
        <ScoreValueLayout
          key="scoreVal-GCS"
          name="GCS"
          selectedScore={selectedScore}
          setSelectedScore={setSelectedScore}
          score={scores.GCS}
          disabled={true}
        />
        <ScoreValueLayout
          key="scoreVal-AIS"
          name="AIS"
          selectedScore={selectedScore}
          setSelectedScore={setSelectedScore}
          score={scores.AIS}
          disabled={true}
        />
        <ScoreValueLayout
          key="scoreVal-ISS"
          name="ISS"
          selectedScore={selectedScore}
          setSelectedScore={setSelectedScore}
          score={scores.ISS}
          disabled={true}
        />
        <ScoreValueLayout
          key="scoreVal-RTS"
          name="RTS"
          selectedScore={selectedScore}
          setSelectedScore={setSelectedScore}
          score={scores.RTS}
          disabled={true}
        />
        <ScoreValueLayout
          key="scoreVal-TRISS"
          name="TRISS"
          selectedScore={selectedScore}
          setSelectedScore={setSelectedScore}
          score={scores.TRISS}
          disabled={true}
        />
        <ScoreValueLayout
          key="scoreVal-MESS"
          name="MESS"
          selectedScore={selectedScore}
          setSelectedScore={setSelectedScore}
          score={scores.MESS}
          disabled={true}
        />
        <ScoreValueLayout
          key="scoreVal-GHOISS"
          name="GHOISS"
          selectedScore={selectedScore}
          setSelectedScore={setSelectedScore}
          score={scores.GHOISS}
          disabled={true}
        />
      </Row>
    </Form>
  );
};

type ScoreValueType = {
  value: number;
  setValue: (val: number) => void;
};

export const ScoreValueLayout = ({
  name,
  selectedScore,
  setSelectedScore,
  score,
  disabled,
}: {
  name: string;
  selectedScore: string;
  setSelectedScore: (scoreName: string) => void;
  score: ScoreValueType;
  disabled?: boolean;
}) => {
  const scoreValStyle: React.CSSProperties = {
    cursor: "pointer",
    border: 0,
    backgroundColor: selectedScore === name ? "#0DCAF0" : "#F0F0F0",
  };

  return (
    <Col span={6}>
      <div
        style={{
          border: 0,
          backgroundColor: selectedScore === name ? "#0DCAF0" : "#F0F0F0",
        }}
        // className={selectedScore === name ? "bg-info" : ""}
      >
        <div className="row">
          <Form.Item
            label={name}
            name={"score-" + name}
            style={scoreValStyle}
            className="col-md-9 m-0"
          >
            <span className="mx-1"> {score.value}</span>
          </Form.Item>
          <i style={scoreValStyle} className="col-md-2">
            <FaPenAlt
              color={selectedScore === name ? "" : BS_BLUE}
              onClick={() => {
                if (!(selectedScore === "GCS")) {
                  // score.setValue(score.value);
                  setSelectedScore(name);
                } else {
                  notification.info({
                    message: "Info",
                    description: "Fonctionnalité bientot disponible",
                  });
                }
              }}
            />
          </i>
        </div>
      </div>
    </Col>
  );
};

export const ScoreTitleLayout = ({ score }: { score: any }) => (
  <div className="mb-0 row">
    <h3 className="text-primary">
      {score.title} ({score.code})
    </h3>
    <em> {score.link} </em>
    <span>
      {score.description} <br />
    </span>
  </div>
);

const selectScoreLayout = (
  selectedScore: string,
  scoreGCS?: any,
  scoreISS?: any,
  scoreRTS?: any,
  scoreTRISS?: any,
  scoreMESS?: any,
  scoreGHOISS?: any
) => {
  switch (selectedScore) {
    case "GCS":
      return {
        score: SCORE_GCS,
        layout: (
          <ScoreGCSLayout
            key="gcs-score"
            score={SCORE_GCS}
            setValue={scoreGCS}
          />
        ),
      };
    default:
      return {
        score: null,
        layout: <ScoreComingSoon />,
      };
    // break;
  }
};

export const ScoreLayout = ({
  selectedScore,
  scoreGCS,
  scoreISS,
  scoreRTS,
  scoreTRISS,
  scoreMESS,
  scoreGHOISS,
}: {
  selectedScore: string;
  scoreGCS?: any;
  scoreISS?: any;
  scoreRTS?: any;
  scoreTRISS?: any;
  scoreMESS?: any;
  scoreGHOISS?: any;
}) => {
  const { score, layout } = selectScoreLayout(
    selectedScore,
    scoreGCS,
    scoreISS,
    scoreRTS,
    scoreTRISS,
    scoreMESS,
    scoreGHOISS
  );
  return selectedScore === "" ? (
    <NoScoreSelected />
  ) : score ? (
    <div className="row">
      <div className="col-md-8">{layout}</div>
      <div className="col-md-4 bg-light p-1">
        <h4 className="text-dark mt-1 text-center text-muted">
          Interprétation
        </h4>
        <Space direction="vertical">
          {score.interpretation.map((item) => (
            <Alert
              message={item.description}
              type={item.level}
              showIcon
              key={"alt-gcs-" + item.min}
            />
          ))}
        </Space>
      </div>
    </div>
  ) : (
    <ScoreComingSoon />
  );
};

export const NoScoreSelected = () => (
  <div className="row p-5 text-center">
    <FaFlagCheckered size={32} />
    <span className="mx-auto text-center">Selectionner un score</span>
  </div>
);

export const ScoreComingSoon = () => (
  <div className="row p-5 text-center">
    <FaChevronCircleRight color={BS_BLUE} size={72} className="mb-5" />
    <span className="mx-auto text-center display-5">Bientot disponible</span>
  </div>
);

// GCS
export const ScoreGCSLayout = ({
  score,
  setValue,
}: {
  score: TYPE_SCORE_GCS;
  setValue?: any;
}) => {
  const [item1, setItem1] = useState(0);
  const [item2, setItem2] = useState(0);
  const [item3, setItem3] = useState(0);
  const [scoreValue, setScoreValue] = useState(0);

  useEffect(() => {
    setScoreValue(item1 + item2 + item3);
    setValue(item1 + item2 + item3);
  }, [item1, item2, item3, setValue]);

  const handleSelectChange = (itemName: string, val: number) => {
    itemName === "ODY"
      ? setItem1(val)
      : itemName === "RV"
      ? setItem2(val)
      : itemName === "RM"
      ? setItem3(val)
      : console.log("pas d'item correspondant à ", itemName);
  };

  return (
    <div>
      <ScoreTitleLayout score={score} />
      <div className="row mb-0">
        <div>
          {score.items.map((item) => (
            <div>
              <Form.Item label={item.title} key={"it-" + item.name}>
                <Select
                  onChange={(val: number) => handleSelectChange(item.name, val)}
                >
                  {item.scale.map((scale) => (
                    <Select.Option
                      value={scale.value}
                      key={"opt-" + scale.name}
                    >
                      {/* // Display one   */}
                      <Text key={"sco-text-" + scale.name}>
                        {scale.name.charAt(0).toUpperCase() +
                          scale.name.slice(1)}
                      </Text>
                      <br />
                      <Text type="secondary" className="text-wrap">
                        {scale.description}
                      </Text>
                      {/* Display two */}
                      {/* <Tooltip title={scale.description}>
                          <span>
                            {scale.name.charAt(0).toUpperCase() +
                              scale.name.slice(1)}
                          </span>
                        </Tooltip> */}
                    </Select.Option>
                  ))}
                </Select>
              </Form.Item>
            </div>
          ))}
        </div>
        <div className="ms-auto col-md-8">
          <label
            htmlFor="score_gcs_value"
            className="mx-3 text-primary fs-4 mb-0"
          >
            TOTAL
          </label>
          <Input
            name={CODE_RUBRIQUES.SCORE_GCS}
            key={"score_gcs_value"}
            value={scoreValue + " / 15"}
            className="col-md-4 mb-3 text-center fs-4 text-dark"
            disabled
          />
        </div>
      </div>
    </div>
  );
};

